@mixin hideTapHighlightColor() {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@mixin hardwareAccel() {
  transform: translateZ(0);
}

@mixin improveAntiAlias() {
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}

@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200..900;1,200..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Anton&family=Bebas+Neue&family=New+Amsterdam&family=Rubik+Mono+One&display=swap");
.fn {
  font-family: "Bebas Neue", sans-serif;
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0.2ch;
}

.scp {
  font-family: "Source Code Pro", monospace;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.stock-ticker {
  font-size: 15px;
  padding-block: 8px;
  overflow: hidden;

  --gap: 20px;
  display: flex;
  gap: var(--gap);
}

.stock-ticker ul {
  list-style: none;
  flex-shrink: 0;
  min-width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: var(--gap);

  animation: scroll 120s linear infinite;
}

.stock-ticker:hover ul {
  animation-play-state: paused;
}

@keyframes scroll {
  to {
    transform: translateX(calc(-100% - var(--gap)));
  }
}

.grow-rotate {
  display: inline-block;
  transition-duration: 0.3s;
  transition-property: transform;

  &:hover {
    transform: scale(1.1) rotate(4deg);
  }
}

.highlight {
  color: rgb(255, 253, 56);
  /* Old browsers */
  background: linear-gradient(to right, rgb(255, 253, 56) 50%, rgba(0, 0, 0, 0) 50%);
  background-size: 200% 100%;
  background-position: 100% 100%;
}

.highlighted-text {
  color: blue; /* Warna teks awal */
  transition: color 0.3s ease; /* Animasi perubahan warna */
}

/* Ganti warna teks saat menggulir ke atas */
.highlighted-text:hover {
  color: red; /* Warna teks setelah menggulir ke atas */
}
